@import url("https://fonts.googleapis.com/css?family=Sigmar+One&display=swap");
body {
  background-color: #2f2f2f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.container {
  display: table;
  width: 100%;
  height: 100%;
}

.movies {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.loader {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  padding: 200px;
}

.loader__text {
  font-family: "Sigmar One", cursive;
  display: inline-block;
  font-size: 25px;
  font-weight: 700;
}
