@import url("https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700&display=swap");
body {
  font-family: "Nanum Gothic", sans-serif;
}

.movie-wrapper {
  display: inline-block;
  width: 350px;
  padding: 50px 30px;
}
.movie {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

img {
  border-radius: 5px;
  transform: scale(0.8);
}

.movie__title {
  font-weight: 700;
  padding: 10px;
}

.movie__year,
.movie__summary {
  font-weight: 400;
}
.movie__summary {
  line-height: 20px;
}

.genres {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.genres__genre {
  padding: 8px;
  margin: 2px;
  font-size: 10px;
  background-color: #95afc0;
  color: #2f2f2f;
  border-radius: 4px;
  font-weight: 700;
}
