@import url(https://fonts.googleapis.com/css?family=Nanum+Gothic:400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Sigmar+One&display=swap);
body {
  font-family: "Nanum Gothic", sans-serif;
}

.movie-wrapper {
  display: inline-block;
  width: 350px;
  padding: 50px 30px;
}
.movie {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

img {
  border-radius: 5px;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.movie__title {
  font-weight: 700;
  padding: 10px;
}

.movie__year,
.movie__summary {
  font-weight: 400;
}
.movie__summary {
  line-height: 20px;
}

.genres {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.genres__genre {
  padding: 8px;
  margin: 2px;
  font-size: 10px;
  background-color: #95afc0;
  color: #2f2f2f;
  border-radius: 4px;
  font-weight: 700;
}

body {
  background-color: #2f2f2f;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}

.container {
  display: table;
  width: 100%;
  height: 100%;
}

.movies {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
}

.loader {
  display: table-cell;
  text-align: center;
  vertical-align: middle;
  padding: 200px;
}

.loader__text {
  font-family: "Sigmar One", cursive;
  display: inline-block;
  font-size: 25px;
  font-weight: 700;
}

